
export default {
  computed: {

  },
  data () {
    return {
      typeMy: ['position', 'price', 'score', 'start', 'other'],
      // 
      positionList: [
        {
          name: '/',
          visible: false,
          open: false,
          key: 'neighborhoods'
        },
        {
          name: '',
          visible: false,
          open: false,
          key: 'trainAirport'
        },
        {
          name: '',
          visible: false,
          open: false,
          key: 'metroStation'
        },
        {
          name: '',
          visible: false,
          open: false,
          key: 'landmark'
        },
        {
          name: '',
          visible: false,
          open: false,
          key: 'scenicSpot'
        },
        {
          name: '',
          visible: false,
          open: false,
          key: 'entertainment'
        },
        {
          name: '',
          visible: false,
          open: false,
          key: 'school'
        }
      ],
      positionSelected: '',
      // 
      priceSelected: '',
      priceList: {
        input: {
          start: 0,
          end: 0
        },
        data: [
          {
            label: '200',
            value: '200'
          },
          {
            label: '200-400',
            value: '200-400'
          },
          {
            label: '400-600',
            value: '400-600'
          },
          {
            label: '600-800',
            value: '600-800'
          },
          {
            label: '800-1000',
            value: '800-1000'
          },
          {
            label: '1000-1300',
            value: '1000-1300'
          },
          {
            label: '1300-1600',
            value: '1300-1600'
          },
          {
            label: '1600-2000',
            value: '1600-2000'
          },
          {
            label: '2000',
            value: '2000'
          }
        ]
      },
      openPrice: false,
      // 
      scoreSelected: '',
      scoreList: [
        {
          label: '4',
          value: '4'
        },
        {
          label: '3',
          value: '3'
        }
      ],
      // 
      startSelected: '',
      startList: [
        {
          label: '3/',
          value: '3/'
        },
        {
          label: '4/',
          value: '4/'
        },
        {
          label: '5/',
          value: '5/'
        }
      ],
      // 
      otherSelected: '',
      otherList: [
        {
          name: '',
          visible: false,
          open: false,
          key: 'brands'
        },
        {
          name: '',
          visible: false,
          open: false,
          key: 'trainAirport'
        }
      ],
      // 
      haveChosen: [
        // {
        //   label: '',
        //   value: 1,
        //   type: ''
        // }
      ],
      hotelFacilitiesList: [
        {
          label: 'WiFi',
          value: 'WiFi'
        },
        {
          label: '',
          value: ''
        },
        {
          label: '',
          value: ''
        },
        {
          label: '',
          value: ''
        },
        {
          label: '',
          value: ''
        },
        {
          label: '',
          value: ''
        },
        {
          label: 'SPA',
          value: 'SPA'
        },
        {
          label: '',
          value: ''
        },
        {
          label: '',
          value: ''
        },
        {
          label: '',
          value: ''
        }
      ]
    }
  },
  watch: {
  },
  mounted () {

  },
  methods: {
    // 
    onPriceSure () {
      let name = this.priceList.input.start + '-' + this.priceList.input.end
      let obj = {
        label: name,
        value: name
      }
      this.onSelectedChange(obj, 'price')
    },
    // -
    onSelectedChange (option, type) {
      // typeMy: ['position', 'price', 'score', 'start', 'other'],
      switch (type) {
        case 'position': // 
          this.positionSelected = option.name
          this.positionIconInit(-1)

          let positionObj = {
            label: option.name,
            value: option.name,
            type: type
          }
          this.addSelected(positionObj)
          break
        case 'price':// 
          this.priceSelected = option.value
          let priceObj = {
            label: option.label,
            value: option.value,
            type: type
          }
          this.addSelected(priceObj)
          break
        case 'score': // 
          this.scoreSelected = option.value
          let scoreObj = {
            label: option.label,
            value: option.value,
            type: type
          }
          this.addSelected(scoreObj)
          break
        case 'start': // 
          this.startSelected = option.value
          let startObj = {
            label: option.label,
            value: option.value,
            type: type
          }
          this.addSelected(startObj)
          break
        case 'other': // 
          this.otherSelected = option.name
          this.otherIconInit(-1)
          const otherObj = {
            label: option.name,
            value: option.name,
            type: 'other'
          }
          this.addSelected(otherObj)
          break
      }
    },
    // 
    addSelected (option) {
      let index = -1
      this.haveChosen.forEach((item, i) => {
        if (item.type === option.type) {
          index = i
        }
      })
      if (index !== -1) {
        this.haveChosen.splice(index, 1, option)
        // 
      } else {
        this.haveChosen.push(option)
      }
    },
    // ------------------------//
    // 
    onUnlimitedClick (type) {
      this.removeSelected({type})
    },
    // 
    removeSelected (option) {
      let index = -1
      this.haveChosen.forEach((item, i) => {
        if (item.type === option.type) {
          index = i
        }
      })
      if (index !== -1) {
        this.haveChosen.splice(index, 1)
      }
      // typeMy: ['position', 'price', 'score', 'start', 'other'],
      switch (option.type) {
        case 'position':
          this.positionSelected = ''
          this.priceList.input.start = 0
          this.priceList.input.end = 0
          break
        case 'price':
          this.priceSelected = ''
          break
        case 'score':
          this.scoreSelected = ''
          break
        case 'start':
          this.startSelected = ''
          break
        case 'other':
          this.otherSelected = ''
          break
      }
      this.oncloseSelectedColor(option.type)
    },
    // 
    clearAll () {
      this.haveChosen = []
      this.positionSelected = ''
      this.priceList.input.start = 0
      this.priceList.input.end = 0
      this.priceSelected = ''
      this.scoreSelected = ''
      this.startSelected = ''
      this.otherSelected = ''
      this.oncloseSelectedColor('position')
      this.oncloseSelectedColor('other')
    },
    //  icon
    positionIconInit (index) {
      this.positionList.forEach((item, i) => {
        if (index !== i) {
          item.visible = false
        }
      })
    },
    otherIconInit (index) {
      this.otherList.forEach((item, i) => {
        if (index !== i) {
          item.visible = false
        }
      })
    },
    // filter tabcoloropen
    oncloseSelectedColor (type) {
      switch (type) {
        case 'position':
          this.positionList.forEach((item) => {
            item.open = false
          })
          break
        case 'other':
          this.otherList.forEach((item) => {
            item.open = false
          })
          break
      }
    }
  }
}



